/* ===== Scrollbar CSS ===== */
  /* Firefox */
  * {
    scrollbar-width: auto;
    scrollbar-color: linear-gradient(180deg, #d0368a 10%, #708ad4 99%);
	scroll-behavior: smooth;
  }

  /* Chrome, Edge, and Safari */
  *::-webkit-scrollbar {
    width: 16px;
  }

  *::-webkit-scrollbar-track {
    background: #000;
  }

  *::-webkit-scrollbar-thumb {
	background-image: linear-gradient(180deg, #d0368a 10%, #708ad4 99%);
	box-shadow: inset 2px 2px 5px 0 rgba(#fff, 0.5);
	border-radius: 100px;
 
  }

.cursor {
	position: fixed;
	left: 0;
	top: 0;
	width: 32px;
	height: 32px;
	border-radius: 16px;
	mix-blend-mode: difference;
	background-color: white;
	z-index: 99;
	pointer-events: none;
  }


.parallax {
	overflow: hidden;
	letter-spacing: -2px;
	line-height: 0.8;
	margin: 0;
	white-space: nowrap;
	display: flex;
	flex-wrap: nowrap;
	margin-bottom: 10px;
}


.parallax .scroller {
	font-family: 'Roboto Mono', monospace;
	/* font-family: 'VT323', monospace; */
	letter-spacing: 5px;
	font-style: italic;
	font-weight: 700;
	font-size: 80px;
	line-height: 110%;
	text-transform: uppercase;
	display: flex;
	white-space: nowrap;
	display: flex;
	flex-wrap: nowrap;
}

.parallax span {
	display: block;
	margin-right: 30px;
	color: white;
	
	/* margin:10; */
}